/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */

import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';
// @mui
import {
  Card,
  Table,
  TableRow,
  TableBody,
  TableCell,
  Container,
  TableContainer,
  TablePagination,
  Switch,
  Button,
  Backdrop,
  CircularProgress
} from '@mui/material';

// format
import FormControlLabel from '@mui/material/FormControlLabel';
// hooks
import useSettings from '../../hooks/useSettings';
import Iconify from '../../components/Iconify';
// components
import Page from '../../components/Page';
import Scrollbar from '../../components/Scrollbar';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';

// sections
import { AdexListHead ,UserMoreMenu} from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 
import { PATH_ADMIN } from '../../routes/paths';

// ----------------------------------------------------------------------

const TABLE_HEAD = [

{ id: 'app_name', label: 'APP NAME', alignRight: false },
{id: 'app_version', label: 'APP VERSION', alignRight: false},
{id: 'platform', label: 'PLATFORM', alignRight: false},
{id: 'download', label: 'DOWNLOAD', alignRight: false},

];

// ----------------------------------------------------------------------

export default function AdminDownloadApp() {
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
  const { enqueueSnackbar } = useSnackbar();
  const [order, setOrder] = useState('asc');
  const [load, SetLoad] = useState(true)
  const [orderBy, setOrderBy] = useState('id');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [dense, Setdense] = useState(false);
  const AccessToken =  window.localStorage.getItem('accessToken');
  useEffect(()  => {
    const initialize = async () => {
  axios.get(`/api/system/app/softwarwe`).then(data => {
     setUserList(data.data?.app);
     SetLoad(false)
         }).catch(() => SetLoad(false));
        }
        initialize();
  });

  const handleRequestSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };





  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

 
  const handleDeleteUser = (userId) => {
   
            axios.post(`/api/delete/app/${AccessToken}/system`,{
                feature_id:userId
            }).then(() => {
                enqueueSnackbar('deleted')
     const deleteUser = userList.filter((user) => user.id !== userId);
    setUserList(deleteUser);  
            }).catch(error => {
                if(error.data?.message){
                    enqueueSnackbar(error.data.message, { variant: 'error' })   
                }else if(error?.message){
                    enqueueSnackbar(error.message, { variant: 'error' })   
                }else{
                    enqueueSnackbar('An Error Occured', { variant: 'error' })   
                }
               
            });
      
  };

   

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - userList.length) : 0;





  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="App Download">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="System Apps"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'download-app' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.general.newapp_download}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              Add New App
            </Button>
          }
        />
        <Card>
      
          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <AdexListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  onRequestSort={handleRequestSort}
                />

                <TableBody>
                  {userList.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    const { id, app_name, app_version, platform, app_link  } = row;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                      >
                        <TableCell align="left">{app_name}</TableCell>
                        <TableCell align="left">{app_version}</TableCell>
                        <TableCell align="left">{platform}</TableCell>
                       <TableCell align="left">
                       <Button href={app_link} target="_blank" rel="noopener" variant="contained">
        Download
      </Button>
                       </TableCell>
                       <TableCell align="right">
                          <UserMoreMenu onDelete={() => handleDeleteUser(id)} userName={'no_edit'} />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                  
                </TableBody>

              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
         
       <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={userList.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) => setPage(page)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
            
        </Card>
        <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
      </Container>
    </Page>
  );





}

// ----------------------------------------------------------------------


 



  

  